<script>
import { computed, ref } from 'vue';
import { Form } from 'vee-validate';
import * as Yup from 'yup';
import useStore from '@/stores';
import InputField from './InputField.vue';

export default {
  components: {
    Form,
    InputField,
  },
  setup() {
    const { orderStore, cartStore } = useStore();
    const { orderResult, orderList, handleSendOrder } = orderStore;
    const { cartData } = cartStore;
    const userRemark = ref('');
    const phoneRegex = /09\d{2}-?\d{3}-?\d{3}/;
    const schema = Yup.object().shape({
      userName: Yup.string().trim().required(''),
      userEmail: Yup.string().trim().email('Email').required('Email'),
      userPhone: Yup.string()
        .test('phone', '10(09...)', (value) => phoneRegex.test(value))
        .required(''),
      userAddress: Yup.string().required(''),
    });

    function handleSubmit(data, { reset }) {
      console.log(data);
      const orderData = {
        name: data.userName,
        email: data.userEmail,
        tel: data.userPhone,
        address: data.userAddress,
        message: userRemark.value,
      };
      handleSendOrder(orderData);
      reset();
    }
    return {
      orderResult: computed(() => orderResult),
      orderList: computed(() => orderList),
      handleSubmit,
      schema,
      userRemark,
      cartListLength: computed(() => cartData.list.length),
    };
  },
};
</script>

<template>
  <section class="container pt-14 pb-20" id="order">
    <h2 class="mb-8 text-2xl text-center"></h2>
    <Form
      action=""
      class="mx-auto space-y-4 md:w-2/3"
      @submit="handleSubmit"
      :validation-schema="schema"
    >
      <div class="flex gap-4 justify-between items-start mb-6">
        <InputField
          name="userName"
          type="text"
          label=""
          placeholder=""
          :disabled="cartListLength === 0"
          :class="{
            'opacity-30 cursor-not-allowed': cartListLength === 0,
          }"
        />
        <InputField
          name="userPhone"
          type="tel"
          maxlength="10"
          label=""
          placeholder=""
          :disabled="cartListLength === 0"
          :class="{ 'opacity-30 cursor-not-allowed': cartListLength === 0 }"
        />
      </div>
      <InputField
        name="userEmail"
        type="email"
        label="Email"
        placeholder="Email"
        :disabled="cartListLength === 0"
        :class="{ 'opacity-30 cursor-not-allowed': cartListLength === 0 }"
      />
      <InputField
        name="userAddress"
        type="text"
        label=""
        placeholder=""
        :disabled="cartListLength === 0"
        :class="{ 'opacity-30 cursor-not-allowed': cartListLength === 0 }"
      />
      <div>
        <label
          for="userRemark"
          class="block mb-2"
          :class="{ 'opacity-30 cursor-not-allowed': cartListLength === 0 }"
          ></label>
        <textarea
          id="userRemark"
          name="userRemark"
          class="w-full form-control"
          v-model="userRemark"
          rows="4"
          placeholder=""
          :disabled="cartListLength === 0"
          :class="{ 'opacity-30 cursor-not-allowed': cartListLength === 0 }"
        ></textarea>
      </div>

      <div class="pt-12">
        <button
          type="submit"
          class="grid place-content-center py-3 mx-auto w-10/12 text-xl
          text-white bg-black rounded transition duration-300 ease-in-out hover:bg-primary-700"
          :class="{
            'opacity-30 cursor-not-allowed': cartListLength === 0,
          }"
          :disabled="cartListLength === 0"
        >
          {{ cartListLength > 0 ? '' : '' }}
        </button>
      </div>
    </Form>
  </section>
</template>
